import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, Validators, ReactiveFormsModule} from '@angular/forms';
import {FormControl, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;
//import {MatFileUploadModule} from '@angular/material';

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./document.component.html",
//	 styleUrls: ['./jobcardlist.component.css'],
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class DocumentComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;
	
fileForm: FormGroup;
Document:File;
@ViewChild('fileDoc') file_Doc;
msg=[];
formData = new FormData();
jobcardfile = new JobCardFileData();
constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {

}
	
ngOnInit() {
	this.fileForm = new FormGroup({	
		'fileDoc' : new FormControl('',[Validators.required]),
		'file' : new FormControl(''),
	});
	}
	  
   get fileDoc(){ return this.fileForm.get('fileDoc')};
   get file(){ return this.fileForm.get('file')};

  populateData(){

 }
	  
ngAfterViewInit(){

 }	

 toggleAnimation(){
	this._router.navigate(['service/jobcard-list']);
 }

	 onSubmit(value) {
		console.log(value);
		const Doc=this.file_Doc.nativeElement;
			
		if(Doc.files && Doc.files[0]){
			this.Document=Doc.files[0]
		}
		const DocFile:File=this.Document;
		console.log(DocFile);
		this.formData.append('file',DocFile);
		console.log(this.formData);
		const headers = new HttpHeaders({'Content-Type':'multipart/formdata'});
		this.jobcardfile['requestType'] = 'add';
		this.jobcardfile['file'] = DocFile;
		console.log(this.jobcardfile);
		this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/service/jobcard', 		                                           this.jobcardfile,
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}
														}
//			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
			 											this.fileForm.reset();
														this.toggleAnimation();
														}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
		  } 
	  }
	

	
export class JobCardFileData {
	file:File;
fileDoc:File;
}


